import React, { useState,useEffect,useReducer, Fragment } from 'react'
import styled from 'styled-components'

import bild1 from '../Img/Anetka 3.jpg'
import Arch from '../Img/Architecture1.jpg'
import ArchMask from '../Img/architecturemask.svg'
import Moon from '../Img/księżyc.jpg'
import moonMask from '../Img/księżyc.png'
import bild6 from '../Img/StreetWch.jpg'
import WchMaska from '../Img/StreetWch.png'
import Star from '../Img/star.svg'
import ScrollToTop from '../Components/ScrollToTop'

const Wrapper = styled.div`
height:100vh;
width:100%;
overflow:hidden;
box-sizing:border-box;
`
const Bild = styled.div`
position:fixed;
background-image: url(${bild6});
background-size:cover;
top:0;
height:100vh;
width:100%;

`
const configTitle = {
    transition: "2.5s",
    color: "orangered"
}

const Title = styled.span`
font-size:80px;
text-align:center;
color:${configTitle.color};
position:absolute;
top:30vh;
left:20%;
transform:translateX(0%)
width:100%;
transitions:  ${configTitle.transition} ease;
-webkit-transitions:  ${configTitle.transition} ease;
-moz-transitions:  ${configTitle.transition} ease;
animation: text ${configTitle.transition} linear  ;
-webkit-animation: text ${configTitle.transition} linear;
-moz-animation: text ${configTitle.transition} linear  ;


@keyframes text {
    0%{transform:translateX(-50%); 
    0%{-webkit-transform:translateX(-50%);}
    0%{-moz-transform:translateX(-50%);}
    100%{transform:translateX(50%)}
    100%{-webkit-transform:translateX(50%)}
    100%{-moz-transform:translateX(50%)}
}


`
const Mask = styled.div`
mask:url(${WchMaska});
mask-size:cover;
-webkit-mask:url(${WchMaska});
-webkit-mask-size:cover;

width:100%;
height:100%;

`
const TextWrapper =styled.div`
position:relative;
;
left:50%;
transform:translate(-50%);
margin:50px 0;
min-height: 100vh;


`

const Text = styled.div.attrs(props=>({
    className: props.className
    
}))`
position:absolute;
z-index:10;
top:60vh;
width:100%;
height:100vh;
background: orange;
min-height:105vh;
border-top-left-radius:20px;
border-top-right-radius:20px;
overflow:hidden;
`

const Form = styled.form.attrs(props=>({className:props.className}))`
position: relative;

&.firstText {
    position:absolute;
    top:2vh;
    left:50%;
    transform:translate(-50%);
    line-height: 1.1em;
    margin:0px auto;
    
    
    &>p {
        margin: 50px auto;
    }
    
&>p::first-letter{
    color:orangered;
    font-size:40px;
    float:left;
    margin-top:8px;
}

}

&.firstText .inputEmail {
    float:left;
    display:inline;
    width:170px;
}

&.firstText .submit {

    background: transparent;
    border:1px solid black;
    color: orangered;
    width:80px;
    margin: 2px 10px;
    float:right;
    display:inline;

}



&.firstText .down {
margin: 100px 0;
}

&.firstText .Message {
    
    height:20vh;
    width:70vw;
    
    
}

&.firstText .Send {
    background: transparent;
    border:1px solid black;
    color: orangered;
    width:80px;
    margin: 2px 10px;
    float:right;
    display:inline;
   
}

&.firstText .inputAdresat {
    float:left;
    display:inline;
    width:170px;
}
`
const RenderBild = () => {
const [emailNews, setEmailNews] = useState({value:''})

    const handleChange=(e)=>{
       
        setEmailNews({value: e.target.value});
    
        
       
    }

    const handleSubmit = e => {
        if(emailNews.value.length>=6){
        alert(`Mail ${emailNews.value} został dodany do Newsleetera`)
        setEmailNews({value: e.target.value})
        setEmailNews({value:''})
        e.preventDefault()
        }
        else {
            alert('Error')
        }
    }


    return(
        <>
        <Wrapper>
    <Bild>  
    <Mask>
    <Title>Contact</Title>
    </Mask>
    </Bild>
    <Text ><TextWrapper> 
         
    <Form  className="firstText">
    
<h1>Write me!</h1>
<p>Jeśli chcesz, możesz zostawić swój mail, mogę informować cię o nowych artykułach i fotkach.
</p>

<input className="inputEmail" type="email" placeholder="Em@il..." onChange={handleChange} value={emailNews.value} />
<button onClick={handleSubmit} className="submit" >Submit</button> 




<p className="down">Lub po prostu napisz do mnie wiadomość. Możesz użyć forumlarza poniżej
</p>
<textarea className="Message" placeholder="Napisz wiadomość" type="text"/>

<input className="inputAdresat" placeholder="Twój Em@il" type="email"/>

<button className="Send">Send!</button>

</Form>
</TextWrapper>
</Text>

</Wrapper>   
</>
    )
}

const Contact = (props) => {
    
useEffect(()=>{




},[])


return(
   
 <>
     <ScrollToTop/>
    <RenderBild/>
   </> 
  
     
)
      
 
}
 
export default Contact;