import React from 'react';
import { useSwipeable } from "react-swipeable";
import styled from "styled-components";

export const NEXT = "NEXT";
export const PREV = "PREV";



export const CarouselContainer = styled.div`
  display: flex;

  transition: ${(props) => (props.sliding ? "none" : "transform 1s ease")};
  transform: ${(props) => {
    if (!props.sliding) return "translateX(calc(-100% - 20px))";
    if (props.dir === PREV) return "translateX(calc(2 * (-100% - 20px)))";
    return "translateX(0%)";
  }};
`;

 export const Wrapper = styled.div`
  
`;

export const CarouselSlot = styled.div`
  flex: 1 0 100%;
  flex-basis: 50%;
  

  order: ${(props) => props.order};
`;
 export const SlideButton = styled.button`
    color: #ffffff;
    font-family: Open Sans;
    font-size: 16px;
    font-weight: 100;
    padding: 10px;
    background-color: #f66f3e;
    border: 1px solid white;
    text-decoration: none;
    display: none;////////
    cursor: pointer;
  margin-top: 20px;
  text-decoration: none;
  float: ${(props) => props.float};

  &:active {
    position: relative;
    top: 1px;
  }
  &:focus {
    outline: 0;
  }
`;




const getOrder = ({ index, pos, numItems }) => {
    return index - pos < 0 ? numItems - Math.abs(index - pos) : index - pos;
  };
  const initialState = { pos: 0, sliding: false, dir: NEXT };
  
  export const Carousel = (props) => {
    const [state, dispatch] = React.useReducer(reducer, initialState);
    const numItems = React.Children.count(props.children);
    const slide = (dir) => {
      dispatch({ type: dir, numItems });
      setTimeout(() => {
        dispatch({ type: "stopSliding" });
      }, 50);
    };
  
    const handlers = useSwipeable({
      onSwipedLeft: () => slide(NEXT),
      onSwipedRight: () => slide(PREV),
      preventDefaultTouchmoveEvent: true,
      trackMouse: true
    });
  
    return (
      <div {...handlers}>
        <Wrapper>
          <CarouselContainer dir={state.dir} sliding={state.sliding}>
            {React.Children.map(props.children, (child, index) => (
              <CarouselSlot
                key={index}
                order={getOrder({ index: index, pos: state.pos, numItems })}
              >
                {child}
              </CarouselSlot>
            ))}
          </CarouselContainer>
        </Wrapper>

        <SlideButton onClick={() => slide(PREV)} float="left">
          Prev
        </SlideButton>
        <SlideButton onClick={() => slide(NEXT)} float="right">
          Next
        </SlideButton>
      </div>
    );
  };
  export default Carousel;

  function reducer(state, { type, numItems }) {
    switch (type) {
      case "reset":
        return initialState;
      case PREV:
        return {
          ...state,
          dir: PREV,
          sliding: true,
          pos: state.pos === 0 ? numItems - 1 : state.pos - 1
        };
      case NEXT:
        return {
          ...state,
          dir: NEXT,
          sliding: true,
          pos: state.pos === numItems - 1 ? 0 : state.pos + 1
        };
      case "stopSliding":
        return { ...state, sliding: false };
      default:
        return state;
    }
  }

