import React, { useState,useEffect,useRef,Suspense } from 'react'
import {Route,Link,Switch} from 'react-router-dom'
import Styled from 'styled-components'

import Portfolio from '../Page/Portfolio'
import Contact from '../Page/Contact'
import Home from '../Page/Home'
import About from '../Page/About'
import Portrety from '../Page/Portrety'
const theme = {
    light: "white",
    dark: "black"
}

const RouteWrap = Styled.div`
background-color:#rgb(255.255.255.255);
position:fixed;
height:0;
width:100%;
top:1%;
z-index:50;
text-align:center;
transition: 1s linear;
opacity:0;


`
const LinkWrap = Styled(Link)`
margin: auto 10px;
text-decoration:none;
position:relative;
top:25%;




&:nth-child(n){
    color:${props=>props.flag?"white" : "black" };
    
}

`



const Nav = () => {
const [close, setClose] = useState(false)
const [flag, setFlag] =useState(true)
const [selected, setSelected]=useState({name: ''})

  function handleClickProps(e){
    
    setSelected( {name: e.currentTarget.textContent})
    if(selected.name==='Portfolio') {
        setFlag(true)
    }
    else if (selected.name==='Home') {
        setFlag(true)
    }
    else if (selected.name==="Contact") {
        setFlag(false)
    }
    else if (selected.name==="About"){
        setFlag(false)
    }
   
    console.log(flag)
}

useEffect(()=>{
   
    const NavPath=()=>{
        const path = document.querySelector('.Rout')
       
 

        path.getBoundingClientRect();
    
        
        window.addEventListener("scroll",function(e){
            const scrollProcent=(document.documentElement.scrollTop+document.body.scrollTop)/(document.documentElement.scrollHeight-document.documentElement.clientHeight);
        
            const draw = Math.round(scrollProcent*200);
            
            if (scrollProcent>=0.10) {
                path.style.transform=`translatey(100%)`
                path.style.opacity="1"
                
            } else if (scrollProcent<=0.99) {
                path.style.transform=`translatey(-100%)`
                path.style.opacity="0"
            }
        })
        }





        NavPath()

},[])

    return ( 
<>

<RouteWrap  className="Rout">

<LinkWrap flag={flag}  className="Home"  onClick={handleClickProps} theme={theme} to="/">Home</LinkWrap>

<LinkWrap flag={flag} className="Portfolio"  onClick={handleClickProps} theme={theme} to="/Portfolio">Portfolio</LinkWrap>

<LinkWrap flag={flag} className="Contact"  onClick={handleClickProps} theme={theme} to="/Contact">Contact</LinkWrap>

<LinkWrap  flag={flag} className="About"  onClick={handleClickProps} theme={theme} to="/About">About</LinkWrap>

</RouteWrap>

<Suspense fallback={<Load>Wczytywanie...</Load>}>
<Switch>

    <Route exact path="/" component={Home}>
        <Home/>
    </Route>

<Route  path="/Portfolio" component={Portfolio}>
<Portfolio />
</Route>

<Route  path="/Contact" component={Contact}>
<Contact />
</Route>

<Route  path="/About" component={About}>
<About />
</Route>

<Route path="/Portrety" component={Portrety}>
            <Portrety/>
          </Route>

</Switch>
</Suspense>
</>
     );
}
 
export default Nav;

const Load= Styled.div`
position:absolute;
background-color:white;
top:50%;
left:50%;
transfrom:translate(-50%,-50%);
color:black;
heigth:200px;
width:100%;
`