import React, { useState,useEffect,useReducer,Suspense } from 'react'
import {Link,Switch,Route} from 'react-router-dom'


import bild1 from '../Img/Anetka 3.jpg'
import bild2 from '../Img/Pati.jpg'
import bild3 from '../Img/Anetka 7.jpg'
import bild4 from '../Img/Anetka 2.jpg'
import bild5 from '../Img/Neon.jpg'
import bild6 from '../Img/tlo.jpg'

import styled from 'styled-components'
import ScrollToTop from '../Components/ScrollToTop'

import {Carousel} from '../Components/SwipeComponent'


const Wraper = styled.div`
height:200vh;
position:absolute;
background: orange;
width:100%;
overflow:hidden;



`

const BoxBildWrapper = styled.div.attrs(props=>({
    className: props.className,
    
}))`
position:relative;
top:30vh;


transition:1s linear;

&.bildAkt {
    top:50vh;
    left:-50%;
    }

    &.bildLandscape {
        top:70vh;
        left:;
    }

`

const BoxBild = styled.img.attrs(props=>({
    className: props.className,
    active: props.active,
src: props.src

}))`
text-align: center;
padding: 10px;
height:40vh;


${({ active }) => active && `

position:relative;
transform:scale(2);

  `}

&.bildAkt {}
&.bildLandscape {}


`
const Modal = styled.div`
height:100vh;
width:100%;
background-color:red;

`

const BoxText = styled.h1.attrs(props=>({
    className: props.className
}))`
position:absolute;

transition: 0.5s linear;
left:-600%;
top:20vh;
white-space: nowrap;
overflow: hidden;


&.textAkt {
    top:80vh;
    left:-100%;
}

&.textLandscape {
    top:140vh;
left:-600%;
}

`
const LinkStyle = styled(Link)`
color:black;
`




const PortBox = props =>{


    const [image, setImage]=useState([

        {id:1,
        src:bild1,
        active:false
        },

        {id:2,
        src:bild2,
        active:false},

        {id:3,
        src:bild3,
        active:false},

        {id:4,
        src:bild4,
        active:false},

        {id:5,
        src:bild5,
        active:false},

        {id:6,
        src:bild6,
        active:false},
    ])

   


  useEffect(()=>{

const ScrollbareComponent = () =>{
    const pathPortretText = document.querySelector(`#textScrollPortret`)
        const pathPortretBild = document.querySelector(`#bildPortretScroll`)
        const pathAktBild = document.querySelector(`#bildAktScroll`)
        const pathAktText = document.querySelector(`#textScrollAkta`)
        const pathLandscapeBild = document.querySelector(`#bildLandscapeScroll`)
        const pathLandscapeText = document.querySelector(`#textScrollLandscape`)
        pathLandscapeBild.getBoundingClientRect();
        pathLandscapeText.getBoundingClientRect();
        pathAktText.getBoundingClientRect();
        pathAktBild.getBoundingClientRect();
        pathPortretBild.getBoundingClientRect();
        pathPortretText.getBoundingClientRect();
        



    



    window.addEventListener("scroll",function(e){
        const scrollProcent=(document.documentElement.scrollTop+document.body.scrollTop)/(document.documentElement.scrollHeight-document.documentElement.clientHeight);


        const draw = Math.round(scrollProcent*100);
        const drawWeit = Math.round(scrollProcent*10);
            if(scrollProcent>=0.99) {
                pathPortretText.style.transform=`translateX(-${drawWeit}%)`
                pathPortretBild.style.transform=`translateX(${draw}%)`
                pathAktBild.style.transform=`translateX(-${draw}%)`
                pathAktText.style.transform=`translateX(${drawWeit}%)`
                pathLandscapeText.style.transform=`translateX(-${drawWeit}%)`
                pathLandscapeBild.style.transform=`translateX(${draw}%)`
                
            } 
            else if(scrollProcent<=1) {
                pathPortretText.style.transform=`translateX(${drawWeit}%)`
                pathPortretBild.style.transform=`translateX(-${draw}%)`
                pathAktBild.style.transform=`translateX(${draw}%)`
                pathAktText.style.transform=`translateX(-${drawWeit}%)`
                pathLandscapeText.style.transform=`translateX(${drawWeit}%)`
                pathLandscapeBild.style.transform=`translateX(-${draw}%)`

            }})



}



ScrollbareComponent()
    

    },[])


    const handleClick =(id,active)=>{
        setImage( image.map(item=>{
        if(id=== item.id) {
          item.active = !item.active
        }
        
       
        
        return item}))
        
        };

    return(
        
<>
     
<ScrollToTop/>

<Wraper className="headerText">

<LinkStyle to="/Portrety"> 
<BoxText className="text" id="textScrollPortret">Portrait Portrait Portrait Portrait Portrait Portrait Portrait Portrait Portrait Portrait Portrait Portrait Portrait Portrait Portrait Portrait Portrait Portrait Portrait Portrait Portrait Portrait Portrait Portrait Portrait Portrait Portrait Portrait Portrait Portrait Portrait Portrait Portrait Portrait Portrait Portrait Portrait Portrait Portrait Portrait Portrait Portrait Portrait Portrait Portrait Portrait Portrait Portrait Portrait Portrait Portrait Portrait Portrait Portrait Portrait Portrait Portrait Portrait Portrait Portrait </BoxText></LinkStyle>



<BoxBildWrapper  id="bildPortretScroll">
<Suspense fallback={<div>Ładowanie...</div>}>
<LinkStyle to="/Portrety"> 
<Carousel>

{image.map(({src,id,active,index})=>
<BoxBild
key={index}
id={id}
className="bildPortret"
src={src}
active={active}
onClick={()=>handleClick(id,active)}>

</BoxBild>)}

</Carousel>
</LinkStyle>
</Suspense>


</BoxBildWrapper>


<BoxText className="textAkt" id="textScrollAkta">Street Photography Street Photography Street Photography Street Photography Street Photography Street Photography Street Photography Street Photography Street Photography Street Photography Street Photography Street Photography Street Photography Street Photography Street Photography Street Photography Street Photography Street Photography Street Photography Street Photography Street Photography Street Photography Street Photography Street Photography Street Photography Street Photography </BoxText>

<BoxBildWrapper className="bildAkt" id="bildAktScroll">
<Carousel>
{image.map(({id,src,active})=>
<BoxBild
className="bildAkt"
key={id}
id={id}
src={src}
active={active}
onClick={()=>handleClick(id,active)}
>

</BoxBild>
)}
</Carousel>
</BoxBildWrapper>

<BoxText className="textLandscape" id="textScrollLandscape">Landscape Landscape Landscape Landscape Landscape Landscape Landscape Landscape Landscape Landscape Landscape Landscape Landscape Landscape Landscape Landscape Landscape Landscape Landscape Landscape Landscape Landscape Landscape Landscape Landscape Landscape Landscape Landscape Landscape Landscape Landscape Landscape Landscape Landscape </BoxText>

<BoxBildWrapper className="bildLandscape" id="bildLandscapeScroll">
<Carousel>
{image.map(({id,src,active})=>
<BoxBild
className="bildLandscape"
key={id}
id={id}
src={src}
active={active}
onClick={()=>handleClick(id,active)}
>

</BoxBild>
)}
</Carousel>
</BoxBildWrapper>




</Wraper>


<Switch>
    <Route path="/Portrety"></Route>
    
</Switch>

</>
    )

}





const Portfolio = () => {
    return ( 
        
    <PortBox></PortBox>
   

    );
}
 
export default Portfolio;