import React, {useState,Suspense,useEffect} from 'react'
import styled from 'styled-components'

import bild1 from '../Img/Anetka 3.jpg'
import bild2 from '../Img/Pati.jpg'
import bild3 from '../Img/Anetka 7.jpg'
import bild4 from '../Img/Anetka 2.jpg'
import bild5 from '../Img/Neon.jpg'
import bild6 from '../Img/tlo.jpg'
import {HiOutlineArrowNarrowLeft} from 'react-icons/hi'
import {useHistory} from 'react-router-dom'
import { LazyLoadImage,LazyLoadComponent } from 'react-lazy-load-image-component';
import 'react-lazy-load-image-component/src/effects/blur.css';
const Wrapp = styled.div`
background:orange;
height:100%;
width:100%;
position:relative;


`
const Title = styled.h1`
text-align:center;
position:relative;
margin-top:60px;

`

const ArrowBack = styled(HiOutlineArrowNarrowLeft)`
font-size: 60px;
position:fixed;
color:white;
top:90%;


transition: 1s ease;
animation: back 0.8s ease-out infinite;

@keyframes back {
    0%{transform:translateX(20%)}
    100%{transform:translateX(-20%)}
}
`

const BildWrapper = styled.div`
height:320vh;

position:relative;

display:flex;
flex-flow:row wrap;
justify-content:center;

`

const Bild = styled(LazyLoadImage)`

flex-basis:auto;


align-items: center;
  justify-content:center;
  display:flex;
transition: 0.2s linear;
overflow:hidden;


object-fit: ${props=>props.active?"contain":"cover"} ;
flex: ${props=>props.active?"1":""};
position: ${props=>props.active?"fixed":""};

height: ${props=>props.active?"120%":""};
width: ${props=>props.active?"100%":"80%"};
padding: ${props=>props.active?"0":"5px"};
background: ${props=>props.active?"orange":""};

`

const Loading = styled.div`
position:fixed;
top:50%;
left:50%;
transform:translate(-50%,-50%);
font-size:30px;
`

const RenderPortrety = () => {
    const history =useHistory();
    const [image, setImage]=useState([

        {id:1,
        src:bild1,
        active:false
        },

        {id:2,
        src:bild2,
        active:false},

        {id:3,
        src:bild3,
        active:false},

        {id:4,
        src:bild4,
        active:false},

        {id:5,
        src:bild5,
        active:false},

        {id:6,
        src:bild6,
        active:false},
    ])
   
useEffect(()=>{

},[])

    const handleClick = (id)=>{
        setImage(image.map(item=>{if(id===item.id){
item.active=!item.active
        }return item }))
    }

    return ( 
        <>
        
    <Wrapp>
    

<BildWrapper>
<Title>Portreit</Title>


{image.map(({id, src, active,index})=>

   <Bild
   placeholder={<Loading>Ładowanie</Loading>}
  
    key={index}
    src={src}
    id={id}
    active={active}
    onClick={()=>handleClick(id,active)}
    >

    </Bild>
)}


<ArrowBack  onClick={()=>history.goBack()}/>


</BildWrapper>

    </Wrapp></> );
    
}
 


const Portrety = () => {
    return ( 
        
        <RenderPortrety/>
        
     );
}
 
export default Portrety;