import React, { useEffect, useState,useRef,useReducer } from 'react'

import Styled from 'styled-components'
import bild from '../Img/tlo.jpg'
import bild1 from '../Img/StreetWch.jpg'
import bild2 from '../Img/Więzienie-01.jpeg'
import bild3 from '../Img/Piaskowa_noc.png'
import star from '../Img/star.svg'
import { HiArrowNarrowDown } from "react-icons/hi";
import ScrollToTop from '../Components/ScrollToTop'
import styled from 'styled-components'




const Background = Styled.div`

`

const HomeWraper = Styled.div`
background-image: linear-gradient(45deg, orange,black, #1D3475, LightSkyBlue);

mask-position: center;
background-size: cover;
background-position: center;
background-repeat: no-repeat;
position:relative;
height:190vh;
width:100%;
overflow:hidden;



`

const Bloks = Styled.div.attrs(props=>({
    className: props.className
}))`
background-color:black;
height:200vh;
width:50%;
position:absolute;
transition: 1s linear;
top:0;

&.links {
    right:0;
    background-color:black;
    z-index:5;
}

&.rechts {
    
left:0;
background-color:black;
z-index:5;
}

&.message {
    transition: 2s linear;

    height: 20vh;
    width:40vh;
    background-color: rgba(0,0,0,0.7);
    top:80%;
    left:50%;
    transform:translate(-50%,-50%);
    z-index:15;
color:white;
overflow:hidden;
}

&.message p {
    text-algin:center;
    margin: 50px 50px;

}

&.message span {
    top:5%;
    left:5%;
    position:absolute;
    cursor:pointer;
    z-index:15;
}

`

const ScrollDown = Styled(HiArrowNarrowDown)`

position:absolute;
top:10vh;
font-size:50px;
opacity:0;
left:50%;
transform:translate(-50%,100%);
color:white;
z-index:10;
transition: opacity 1s linear 5s;
animation: down 3s ease-out infinite;

@keyframes down {
    0% {
       
        transform:translate(-50%,100%);
      
    }
    100% {
        opacity:1;
        transform:translate(-50%,150%)
        
    }
}


`

const EfectBox=Styled.div`

top:110vh;
width:100%;
position:sticky;
display:flex;
flex-flow: row wrap;
justify-content:center;
align-items: center;
z-index:5;

`
const Box = Styled.div.attrs(props=>({
    className: props.className
}))`
transition: 1s linear;
position:absolute;
font-family: 'Merienda One';
text-align: center;
display:flex;
flex-flow: row wrap;
justify-content:center;
align-items: center;
z-index:1;

&:nth-child(1) {
    color:orange;
    font-size:80px;
    
    
}

&:nth-child(1) {
    transform:translatex(30%);
}

& p.Ra {
    transition: opacity .5s 1s;
    font-size:38px;
   float:right;
   opacity:0;
   
}

&:nth-child(2) {
    font-size:80px;
color:white;
transform:translatex(35%);

}

& p.Kr {
    color:white;
    float:right;
    font-size:38px;
    opacity:0;
    
    transition: opacity .5s linear 1s;
}

&:nth-child(3) {
    font-size:30px;
    transform:translate(0,250%);
    transition: transform 5s, opacity: 3s;
    color:chocolate;
    

}
&:nth-child(3):first-letter {
    font-size:40px;
}



`

const Polygon = Styled.div.attrs(props=>({
    className: props.className,
    
    
}))`
background-image: url(${(props)=>props.src});
background-size: 140vh, cover;
z-index:10;
background-position: center center;
background-repeat:no-repeat;



position: absolute;
left: 50%;
transform:translate(-50%);
top: 95vh;
width: 100%;
height:100vh; 
transition: opacity 1s linear ;
animation:  2s linear infinite;
opacity:0;


&.active {
    opacity:1;
}

@keyframes showBild {

    100%{opacity:1}
}



mask-image: linear-gradient(to bottom, transparent 1%, black);
mask-size: 100vmin;
mask-position: center;
}




`

const Bild = styled.img`
height:100vh;
width:100%;
positon:absolute;
z-index:100;
top:100vh;
`
const Rama = Styled.div`
height:80vh;
width:70%;
border-radius: 1% 1%;
position:absolute;
top:100vh;
left:50%;
transform:translate(-50%);
transition:1s ease;

border: 30px solid rgba(255,255,255,.4);
z-index:1;

`

const DivPath=()=>{
    const pathL = document.querySelector('.links')
    const pathR =document.querySelector('.rechts')
    const pathMessage = document.querySelector('.message')
    
    pathL.getBoundingClientRect();
    pathR.getBoundingClientRect();
    pathMessage.getBoundingClientRect();
    
    window.addEventListener("scroll",function(e){
        const scrollProcent=(document.documentElement.scrollTop+document.body.scrollTop)/(document.documentElement.scrollHeight-document.documentElement.clientHeight);
    
        const drawLenght=scrollProcent;
        
        if (scrollProcent>=0.10) {
            pathL.style.transform="translate(100%)"
            pathR.style.transform="translate(-100%)"
            pathMessage.style.transform="scale(1) translate(-50%,-50%)"

            
        } else {
            pathL.style.transform="translate(0)"
            pathR.style.transform="translate(0)"
            pathMessage.style.transform="scale(0) translate(-250%,-250%)"
        }
    })
    }





const Home = () => {
const [close, setClose]=useState(true)

const endref=useRef()

const [image, setImage] = useState([
    {
       id:1,
    src:bild1,
    active:false

},
{id:2,
    src:bild2,
    active:false

},
{
    id:3,
    src:bild3,
    active:false

}])

const [curr, setCurr]= useState(0)
        
const { length } = image



const goToNext = ()=> {
    
     
    setCurr(curr === length - 1 ? 0 : curr +1)
    
}
   

useEffect(()=>{
    
  
 setTimeout(goToNext, 10000)
return ()=>{
clearTimeout(goToNext)


}

if(!Array.isArray(image) || image.length <=0) {
    return null
}

})





useEffect(()=>{



    const Arrow =()=>{
        const pathArrow = document.querySelector(`#arrow`)
        pathArrow.getBoundingClientRect();
        window.addEventListener("scroll",function(e){
            const scrollProcent=(document.documentElement.scrollTop+document.body.scrollTop)/(document.documentElement.scrollHeight-document.documentElement.clientHeight);
            if(scrollProcent>=0.10) {
                pathArrow.style.display='none'
            } 
            else{
                pathArrow.style.display='block'
            }
    })}

const RKF = ()=> {
    const pathR = document.querySelector(`.R`)
    const pathRa = document.querySelector(`.Ra`)
    const pathK = document.querySelector(`.K`)
    const pathKr = document.querySelector(`.Kr`)
    const pathF = document.querySelector(`.F`)
    const pathFo = document.querySelector(`.Fo`)
    
    pathR.getBoundingClientRect();
    pathK.getBoundingClientRect();
    pathF.getBoundingClientRect();
    
    window.addEventListener("scroll",function(e){
        const scrollProcent=(document.documentElement.scrollTop+document.body.scrollTop)/(document.documentElement.scrollHeight-document.documentElement.clientHeight);
        
        const draw = Math.round(scrollProcent*200);
        if(scrollProcent>=0.70) {
            


            pathR.style.transform=`translateX(calc(100% - ${draw}%))`
            pathRa.style.opacity="1"

            pathK.style.transform=`translateX(calc(-155% + ${draw}%))`
            pathKr.style.opacity="1"

            pathF.style.opacity="1"
            pathF.style.transform=`translate(0,200%)`     
                 

        }
        else if(scrollProcent<=0.90) {
                   
            pathR.style.transform=`translatex(30%)`
         pathRa.style.opacity="0"

            pathK.style.transform=`translatex(35%)`
            pathKr.style.opacity="0"

            pathF.style.opacity="0"
            pathF.style.transform=`translate(0,1000%)`
               

        }
        else{
            
           
        }

        
})
}


const Berg = ()=>{

    const pathBerg = document.querySelector('.poly')
    pathBerg.getBoundingClientRect();

    window.addEventListener("scroll",function(e){

        const scrollProcent=(document.documentElement.scrollTop+document.body.scrollTop)/(document.documentElement.scrollHeight-document.documentElement.clientHeight);

        const draw = Math.round(scrollProcent*100);
        if (scrollProcent>=0.60) {
            pathBerg.style.transform="translate(-50%,0)"
        
        
        }
            
            else {
                pathBerg.style.transform=`translate(-50%,-${draw}px)`
               
            }
        }


)
}

Arrow()
RKF()
DivPath()
Berg()

},[])





const handleClick=()=>  (
setClose(prevState=>!prevState)
)
const scrollDownApp = ()=>{
    window.scrollTo({left:0, top:document.body.scrollHeight,behavior: "smooth"})}

    return ( 
        <>
        <ScrollToTop/>
<Background>
<HomeWraper>

<ScrollDown id="arrow" onClick={scrollDownApp}></ScrollDown>


<EfectBox>
<Box className="R">R<p className="Ra">afal</p></Box>

<Box className="K">K<p className="Kr">rzywicki</p></Box>

<Box className="F">Photography</Box>


</EfectBox>

    <Bloks 
    className="message" 
    style={{
        display:close?"block":"none"
    }}><span onClick={handleClick}>X</span><p>W doskonałej fotografii chodzi o głębie uczucia, nie o głębie ostrości.</p></Bloks>
    
<Bloks className="rechts"/>
<Bloks className="links" />
     

{ image.map((image,i)  =>
(<Polygon 
className={i===curr?"active":""}
key={image.id}
src={image.src}
/> ))
}
<Rama className="poly" />
<div ref={endref}></div>
</HomeWraper>
</Background>
        </>
     );
}
 
export default Home;